import React, { useEffect, useMemo } from 'react'
// import AniLink from "gatsby-plugin-transition-link/AniLink"
import { getImage, StaticImage } from 'gatsby-plugin-image';
import { useToken } from '@chakra-ui/react';
import { useFormikContext } from 'formik';
import styled from '@emotion/styled'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Button from '../../components/Button'
import Container from '../../components/Container'
// import SwipeLink from '../../components/SwipeLink'
import PaintLink from '../../components/PaintLink'

import Layout from '../Layout';
import { responsive } from '../../components/ThemeProvider/theme'
// import Banner from './Banner';

const StyledLayout = styled(Layout)`
@media (min-height: 630px) and (min-width: 784px) {
  .footer {
    padding-top: 0px;
    padding-bottom: 0px;
    position: absolute;
    transform: translateX(-50%);
    background: transparent;
  }
}
@media (min-height: 811px) and (min-width: 784px) {
  height: 812px;
}
@media (min-height: 630px) and (max-width: 784px) {
  .main-wrapper {
    height: calc(100% - 57.25px);
  }
}
`


// const hanldeImages = layer => ({
//   ...layer,
//   image: getImage(layer.image),
// })

const IndexPage = () => {
  const { resetForm } = useFormikContext()
  const [teal] = useToken('colors', ['custom.teal'])
  // const mobileImages = useMemo(() => allMobileJson.nodes.map(hanldeImages).reverse(), [allMobileJson])
  useEffect(() => {
    setTimeout(resetForm)
  }, [resetForm])
  return (
    <StyledLayout
      footerProps={{
        bg: 'custom.lightBlueBg',
        maxWidth: responsive('auto', '375px'),
        mx: 'auto',
        py: '1em',
        position: 'static',
        left: '50%',
        // transform: responsive('none', height > 630 ? 'translateX(-50%)' : 'none'),
        bottom: '2em',
      }}
      logoWidth="3.25em"
      noPt
      headerColor="white"
      height="100%"
    >
      <Box className="main-wrapper" bg="custom.lightBlueBg" height="100%" maxWidth={responsive('auto', '375px')} m="auto">
        {/* <Banner width={834} height={717} images={mobileImages} /> */}
        <StaticImage src="../../image/banner.jpg" alt="主視覺" layout="fullWidth" placeholder="none" />
        <Box mt={responsive('0', '3em')} zIndex={5}>
          <Container textAlign="center">
            <Text
              fontSize="1em"
              fontWeight="bold"
              color="custom.darkBlue"
              textAlign="center"
              lineHeight="1.75"
            >
              悶了好久，超想出國玩一波！<br />
              憑直覺決定 6 件生活小事，<br />
              開門抵達和你最配的旅行地！
            </Text>
            <Button.Yellow
              as={p => <PaintLink hex={teal} {...p} />}
              my={responsive('1.375rem', '1.5em')}
              to="q/1"
              maxWidth="180px"
              height="50px"
              fontSize="1em"
            >
              進入 OMG 任意門
            </Button.Yellow>
            <Text
              fontSize="0.875em"
              color="custom.textGray"
              textAlign="center"
            >
              完成測驗領取驚喜彩蛋，<br />
              9.10 前騎乘 GoShare，<br />
              憑發票就抽歐美十大度假勝地雙人來回機票！
            </Text>
          </Container>
        </Box>
      </Box>
    </StyledLayout>
  )
}

export default IndexPage
